import React from "react";

function Contact () {
    return(
        <div className="Contact" id="Contact">
            <div className="container">
                <div className="contact-container">
                    <div className="contact-container-text">
                        <h1>Contact Us</h1>
                        <h3>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Adipisci illo laudantium magni! Adipisci dicta ducimus, explicabo iusto laborum quidem voluptatibus!</h3>
                    </div>


                    <div className="contact_1-row">
                        <input type="text" placeholder={"First Name"}/>
                        <input type="text" placeholder={"Last Name"}/>
                    </div>
                    <div className="contact_2-row">
                        <input type="text" placeholder={"Email"}/>
                        <input type="text" placeholder={"Phone"}/>
                    </div>
                    <div className="contact_3-row">
                        <input type="text" placeholder={" Message"}/>
                    </div>
                    <button className="contact-send-btn" >Send</button>
                </div>
            </div>
        </div>
    );
}
export default Contact;