import React from "react";
import menu_logo from "./Images/2.png";
import {Link} from 'react-scroll';



function Menu() {
    return(
        <div className="Menu">
            <div className="container">
             <div className="menu-container">
                <div className="menu-img">
                    <img src={menu_logo} alt=""/>
                </div>
                <div className="menu-navigation">
                    <Link to={"Info"}
                    smooth={true}>
                       About us
                    </Link>
                    <Link to={"Services"} smooth={true}>
                        Service
                    </Link>
                    <Link to={"Reviews"} smooth={true}>
                        Reviews
                    </Link>
                    <Link to={"Contact"} smooth={true}>
                       Contact Us
                    </Link>

                </div>
                <div className={"menu-btn"} smooth={true}>
                    sigh up
                </div>
             </div>
            </div>
        </div>

    );

}
export default Menu;